import gql from 'graphql-tag';
import * as React from 'react';
import * as ApolloReactCommon from '@apollo/react-common';
import * as ApolloReactComponents from '@apollo/react-components';
import * as ApolloReactHoc from '@apollo/react-hoc';
export type Maybe<T> = T | null;
export type Omit<T, K extends keyof T> = Pick<T, Exclude<keyof T, K>>;
/** All built-in and custom scalars, mapped to their actual values */
export type Scalars = {
  ID: string,
  String: string,
  Boolean: boolean,
  Int: number,
  Float: number,
  Address: any,
  ByteString: any,
  DateTimeOffset: any,
  Date: any,
  DateTime: any,
  Decimal: any,
  Milliseconds: any,
  Seconds: any,
};

export type Action = {
  __typename?: 'Action',
  arguments: Array<ActionArgumentType>,
};

export type ActionArgumentType = {
  __typename?: 'ActionArgumentType',
  key: Scalars['String'],
  value: Scalars['String'],
};


export type Block = {
  __typename?: 'Block',
  difficulty: Scalars['Int'],
  hash: Scalars['ID'],
  index: Scalars['Int'],
  miner: Scalars['Address'],
  nonce: Scalars['ByteString'],
  previousBlock?: Maybe<Block>,
  timestamp: Scalars['DateTimeOffset'],
  transactions: Array<Transaction>,
};

export type BlockQuery = {
  __typename?: 'BlockQuery',
  block?: Maybe<Block>,
  blocks?: Maybe<Array<Maybe<Block>>>,
};


export type BlockQueryBlockArgs = {
  hash?: Maybe<Scalars['ID']>
};


export type BlockQueryBlocksArgs = {
  desc?: Maybe<Scalars['Boolean']>,
  offset?: Maybe<Scalars['Int']>,
  limit?: Maybe<Scalars['Int']>
};








export type Transaction = {
  __typename?: 'Transaction',
  actions: Array<Action>,
  id: Scalars['ID'],
  nonce: Scalars['Int'],
  publicKey: Scalars['ByteString'],
  signature: Scalars['ByteString'],
  signer: Scalars['Address'],
  timestamp: Scalars['DateTimeOffset'],
  updatedAddresses: Array<Scalars['Address']>,
};
export type BlockCommonFragment = ({ __typename?: 'Block' } & Pick<Block, 'hash' | 'index' | 'difficulty' | 'timestamp'>);

export type BlockListQueryVariables = {
  offset: Scalars['Int'],
  limit: Scalars['Int']
};


export type BlockListQuery = ({ __typename?: 'BlockQuery' } & { blocks: Maybe<Array<Maybe<({ __typename?: 'Block' } & BlockCommonFragment)>>> });

export type BlockByHashQueryVariables = {
  hash?: Maybe<Scalars['ID']>
};


export type BlockByHashQuery = ({ __typename?: 'BlockQuery' } & { block: Maybe<({ __typename?: 'Block' } & Pick<Block, 'miner' | 'nonce'> & { previousBlock: Maybe<({ __typename?: 'Block' } & Pick<Block, 'hash'>)>, transactions: Array<({ __typename?: 'Transaction' } & Pick<Transaction, 'nonce' | 'publicKey' | 'signature' | 'signer' | 'timestamp' | 'updatedAddresses'>)> } & BlockCommonFragment)> });
export const BlockCommonFragmentDoc = gql`
    fragment BlockCommon on Block {
  hash
  index
  difficulty
  timestamp
}
    `;
export const BlockListDocument = gql`
    query BlockList($offset: Int!, $limit: Int!) {
  blocks(desc: true, offset: $offset, limit: $limit) {
    ...BlockCommon
  }
}
    ${BlockCommonFragmentDoc}`;
export type BlockListComponentProps = Omit<ApolloReactComponents.QueryComponentOptions<BlockListQuery, BlockListQueryVariables>, 'query'> & ({ variables: BlockListQueryVariables; skip?: boolean; } | { skip: boolean; });

    export const BlockListComponent = (props: BlockListComponentProps) => (
      <ApolloReactComponents.Query<BlockListQuery, BlockListQueryVariables> query={BlockListDocument} {...props} />
    );
    
export type BlockListProps<TChildProps = {}> = ApolloReactHoc.DataProps<BlockListQuery, BlockListQueryVariables> & TChildProps;
export function withBlockList<TProps, TChildProps = {}>(operationOptions?: ApolloReactHoc.OperationOption<
  TProps,
  BlockListQuery,
  BlockListQueryVariables,
  BlockListProps<TChildProps>>) {
    return ApolloReactHoc.withQuery<TProps, BlockListQuery, BlockListQueryVariables, BlockListProps<TChildProps>>(BlockListDocument, {
      alias: 'withBlockList',
      ...operationOptions
    });
};
export type BlockListQueryResult = ApolloReactCommon.QueryResult<BlockListQuery, BlockListQueryVariables>;
export const BlockByHashDocument = gql`
    query BlockByHash($hash: ID) {
  block(hash: $hash) {
    ...BlockCommon
    miner
    nonce
    previousBlock {
      hash
    }
    transactions {
      nonce
      publicKey
      signature
      signer
      timestamp
      updatedAddresses
    }
  }
}
    ${BlockCommonFragmentDoc}`;
export type BlockByHashComponentProps = Omit<ApolloReactComponents.QueryComponentOptions<BlockByHashQuery, BlockByHashQueryVariables>, 'query'>;

    export const BlockByHashComponent = (props: BlockByHashComponentProps) => (
      <ApolloReactComponents.Query<BlockByHashQuery, BlockByHashQueryVariables> query={BlockByHashDocument} {...props} />
    );
    
export type BlockByHashProps<TChildProps = {}> = ApolloReactHoc.DataProps<BlockByHashQuery, BlockByHashQueryVariables> & TChildProps;
export function withBlockByHash<TProps, TChildProps = {}>(operationOptions?: ApolloReactHoc.OperationOption<
  TProps,
  BlockByHashQuery,
  BlockByHashQueryVariables,
  BlockByHashProps<TChildProps>>) {
    return ApolloReactHoc.withQuery<TProps, BlockByHashQuery, BlockByHashQueryVariables, BlockByHashProps<TChildProps>>(BlockByHashDocument, {
      alias: 'withBlockByHash',
      ...operationOptions
    });
};
export type BlockByHashQueryResult = ApolloReactCommon.QueryResult<BlockByHashQuery, BlockByHashQueryVariables>;